.tableHeaderContainer {
    height: 34px;

    display: grid;
    grid-template-rows: none;
    grid-template-columns: 1fr 2fr 2fr 1fr 2fr 2fr 2fr 2fr 2fr 2fr 1fr 1fr 1fr;

    align-items: center;
    justify-items: flex-start;

    padding-left: 8px;
    padding-right: 12px;
}

.tableHeaderContainer > span {
    margin-left: auto;
}

.tableHeaderContainer > span:first-child {
    margin-left: 0;
}