.tradeBody {
    box-sizing: border-box;
    width: 100%;
    height: 30px;
    border-radius: 10px;

    display: grid;
    grid-template-rows: none;
    grid-template-columns: 1fr 2fr 2fr 1fr 2fr 2fr 2fr 2fr 2fr 2fr 1fr 1fr 1fr;

    align-items: center;
    justify-items: flex-start;

    cursor: pointer;
    margin-bottom: 5px;

    padding: 0 5px;
}

.tradeBody > span {
    margin-left: auto;
}

.tradeBody > span:first-child {
    margin-left: 0;
}

.chartIcon {
    box-sizing: border-box;
    font-size: 17px;
    line-height: 13px;
    padding-left: 2px;
}

.trade__dropPlace {
    width: 96%;
    margin-left: auto;
    margin-right: auto;
    background: rgba(90, 90, 90, 1);
    border-radius: 3px;
    height: 6px;
    margin-bottom: 5px;
}