.notificationBody {
    box-sizing: border-box;
    width: 240px;
    height: 64px;
    border-radius: 10px;
    padding: 5px;

    display: flex;
    flex-flow: column wrap;
    justify-content: space-between;
    align-items: center;
    align-content: center;

    cursor: pointer;

    background: rgba(50, 50, 50, 0.7);
    margin-bottom: 5px;
}

.notificationRow {
    width: 100%;

    display: flex;
    flex-flow: row wrap;
    justify-content: space-between;
    align-items: center;
    align-content: center;
}