.buttonMain {
    width: 45px;
    cursor: default;
    line-height: 13px;
}

.buttonVolume {
    width: 40px;
    cursor: default;
    line-height: 13px;
}

.buttonVolume:hover,
.buttonMain:hover {
    background: rgba(70, 70, 70, 0.7);
}

.buttonVolume:hover,
.buttonMain:active {
    background: rgba(70, 70, 70, 0.7);
}
