.commonBody {
    width: 100%;
    box-sizing: border-box;
}

.commonContainer {
    margin: 0 auto;
    padding: 0 5px;

    box-sizing: border-box;

    width: 90%;
    max-width: 960px;
    min-width: 750px;
}

.commonFlexBody {
    display: flex;
    justify-content: center;
    align-items: center;
    align-content: center;
}

.commonHeaderButtonMain {
    width: 28px;
    height: 28px;
    border-radius: 10px;
    display: flex;
    justify-content: center;
    align-items: center;
    align-content: center;

    background: rgba(70, 70, 70, 0.7);

    cursor: pointer;
}

.commonHeaderButtonMain:hover {
    background: rgba(90, 90, 90, 0.7);
}

.commonHeaderButtonMain:active {
    background: rgba(30, 30, 30, 0.7);
}

.commonCloseButton {
    position: relative;
    width: 24px;
    height: 22px;
    border-radius: 10px;
    display: flex;
    justify-content: center;
    align-items: center;
    align-content: center;

    background: rgba(70, 70, 70, 0.7);

    cursor: pointer;
}

.commonCloseButton:hover {
    background: rgba(90, 90, 90, 0.7);
}

.commonCloseButton:active {
    background: rgba(30, 30, 30, 0.7);
}

.common__colorFlet {
    background: rgba(45, 45, 45, 1);
}

.common__colorBull {
    background: rgba(30, 130, 30, 1);
}

.common__colorBear {
    background: rgba(130, 30, 30, 1);
}

.backgroundModal {
    position: fixed;
    top: 0;
    left: 0;
    width: 100vw;
    height: 100vh;
    background-color: rgba(50, 50, 50, 0.5);
    z-index: 10;
}

.notificationBody {
    position: absolute;
    width: 245px;
    right: 5px;
    top: 39px;
    max-height: calc(100vh - 80px);
    z-index: 11;
    overflow-x: hidden;
    overflow-y: scroll;
}