.chartBody {
    width: 800px;
    position: fixed;
    top: calc(50vh - 100px);
    left: calc(50vw - 400px);
    background: rgba(40, 40, 40, 1);
    z-index: 21;
    /*//display: flex;*/
    /*flex-flow: column;*/
    /*align-items: center;*/
    /*justify-content: center;*/
    border-radius: 10px;
    padding: 10px;
    box-sizing: border-box;
}

@media (max-width: 1011px) {
    .chartBody {
        width: 80%;
        left: calc(50vw - 40%);
    }
}