.signalsBody {
    background: rgba(60, 60, 60, 1);
    height: 254px;
    overflow-x: hidden;
    overflow-y: scroll;
}

.signalsContainer {
    display: grid;
    grid-template-columns: 1fr 1fr 1fr 1fr 1fr 1fr;
    grid-template-rows: none;
    grid-column-gap: 5px;
    grid-row-gap: 5px;
    padding-top: 5px;
    padding-bottom: 5px;
    place-content: flex-start;
}

@media (max-width: 1011px) {
    .signalsContainer {
        grid-template-columns: 1fr 1fr 1fr 1fr 1fr;
    }
}

