.signalBody {
    box-sizing: border-box;
    min-width: 135px;
    height: 78px;
    padding: 5px;

    border-radius: 10px;

    display: flex;
    flex-flow: column wrap;
    justify-content: space-between;
    align-items: center;
    align-content: center;

    cursor: pointer;
}

.signalRow {
    width: 100%;

    display: flex;
    flex-flow: row wrap;
    justify-content: space-between;
    align-items: center;
    align-content: center;
}